import { Link } from "@chakra-ui/react";
import { HashLink  as ReachLink } from "react-router-hash-link";
import "./Navbar.css";

export default function Navbar() {
  const links = [
    { path: "#home", class: "nav-link  home", title: "Home" },
    { path: "#about", class: "nav-link about ", title: "About" },
    { path: "#skills", class: "nav-link skills ", title: "Skills" },
    { path: "#projects", class: "nav-link projects ", title: "Projects" },
   

    // { path: "#contact", class: "nav-link contact ", title: "Contact" },
    // {
    //   path: "https://drive.google.com/file/d/1zeacaiNntvIbp22U9xJy4mFEdrIsWRhT/view?usp=share_link",
    //   class: "nav-link resume",
    //   title: "Resume",
    // },
  ];

  return (
    <div id="nav-menu" className="n-wrapper">
      <div className="n-left">
        <div className="n-name">
          {`< Son`}
          <span className="halfName">{`ia / >`}</span>
        </div>
      </div>
      <div className="n-right">
        <div className="n-list">
          <ul>
            {links.map((el, i) => {
              return (
                <Link
                  textDecor={"none"}
                  _hover={{
                    cursor: "pointer",
                    color: "var(--orange)",
                    fontWeight:"bold",
                  }}
                  key={i}
                  as={ReachLink}
                  to={el.path}
                  target={el.title == "Resume" ? "_blank" : "_self"}
                >
                  <li>{el.title}</li>
                </Link>
              );
            })}
          </ul>
        </div>
        <Link as={ReachLink} to="#contact" id="contact" className="nav-link contact ">
          <button className="button n-button">Contact </button>
        </Link>
        <Link
          as={ReachLink}
          to="https://drive.google.com/file/d/1zeacaiNntvIbp22U9xJy4mFEdrIsWRhT/view?usp=share_link"
          className="nav-link resume"
        >
          <button className="button n-button">Resume</button>
        </Link>
      </div>
      <div></div>
    </div>
  );
}
