
 import "./Card.css"
export default function Card({html,css,js,react,emoji,heading,details}) {
    
 return(
<div className="card">
        <img src={emoji} alt="" />
        <span style={{fontWeight:"bold",marginTop:"22px"}}>{heading}</span>
        <div style={{display:"flex",marginTop:"2px"}}> 
        <img  src={html} alt="" />
        <img src={css} alt="" />
        </div>
        <div style={{display:"flex",marginTop:"2px"}}>
        <img src={js} alt="" />
        <img src={react} alt="" />
        </div>
        
       
        
        <span>{details}</span>
        
    </div>
 )   

};


export const Backend=({html,css,js,react,emoji,heading,details})=>{
    return(
        <div className="card"style={{gap:"1px",margin: "auto"}} >
        <img style={{marginBottom:"-2rem"}} src={emoji}  alt="" />
        <span style={{fontWeight:"bold"}}>{heading}</span>
        <div style={{display:"flex",marginTop:"2px"}}> 
        <img style={{transform:"scale(0.5)",marginTop:"-40px"}} src={html} alt="" />
        <img style={{transform:"scale(0.5)",marginTop:"-40px",marginLeft:"-50px"}} src={css} alt="" />
        </div>
        <div style={{display:"flex",marginTop:"2px"}}>
        <img style={{transform:"scale(0.5)",marginBottom:"-1rem",marginTop:"-60px"}} src={js} alt="" />
        </div>
        
       
        
        <span style={{marginTop:"10px"}}>{details}</span>

        </div>

    )
} 



export const Tools=({netlify,vercel,figma,githubs,js,react,emoji,heading,details})=>{
    return(
        <div className="card" >
        <img src={emoji} alt="" />
        <span style={{fontWeight:"bold",marginTop:"22px"}}>{heading}</span>
        <div style={{display:"flex",marginTop:"2px"}}> 
        <img style={{transform:"scale(0.5)",marginLeft:"-65px",marginTop:"-40px"}} src={netlify} alt="" />
        <img style={{transform:"scale(0.4)",marginLeft:"-80px",marginTop:"-40px"}} src={githubs} alt="" />
        </div>
        <div style={{display:"flex",justifyContent:"space-evenly"}}>
            <div>
                <img style={{width:"1000px",marginTop:"-40px"}} src={vercel} alt="" />
                </div>
                <div>

        <img style={{transform:"scale(0.4)",marginTop:"-60px",marginBottom:"20px"}} src={figma} alt="" />
                </div>
        </div>
        
       
        
        <span style={{marginTop:"-50px"}}>{details}</span>

        </div>

    )
} 