import "./Skills.css";

import HeartEmoji from "../../img/heartemoji.png";
import Glasses from "../../img/glasses.png";
import Humble from "../../img/humble.png";
import Card, { Backend, Tools } from "../Cards/Card";
import html from "../../img/html.png";
import css from "../../img/css.png";
import js from "../../img/js.png";
import react from "../../img/react.png";
import mongodb from "../../img/mongodb.png";
import nodejs from "../../img/nodejs.png";
import expressjs from "../../img/expressjs.png";
import githubs from "../../img/githubs.png";
import netlify from "../../img/netlify.png";
import vercel from "../../img/vercel.png";
import figma from "../../img/figma.png";
import Sonia_Diwedi from "./Sonia_Diwedi.pdf"

export default function Skills() {
  return (
    <div id="skills">
      <div className="services">
        {/* left side */}
        <div className="awesome">
          <span>My Awesome</span>
          <span>Technical Skills</span>
          <span>
            My preferred tech-stacks for front-end  web development and component design,
            <br />
            back-end web programming and database architecture and
            <br />
            tools for version control, code editing, and deployment.
          </span>
          <a href={Sonia_Diwedi} target="_blank" download> 
          <button id="resume-button-1" className="button s-button">
            
            Download CV
          </button>
          </a>
          <div
            className="blur s-blur1"
            style={{ background: "#ABF1FF94" }}
          ></div>
        </div>
        <div className="cards" >
          <div style={{ left: "26rem",  }}>
          
            <Tools
              emoji={HeartEmoji}
              heading={"Tools"}
              details={"Figma, netlify, Github, vercel"}
              vercel={vercel} githubs={githubs} figma={figma} netlify={netlify}
            />
          </div>
          <div style={{ top: "12rem", left: "-1rem" }}>
            <Card
              emoji={Glasses}
              heading={"Front-End"}
              details={"HTML, CSS, JavaScript, React"}
              html={html}
              css={css}
              js={js}
              react={react}
            />
          </div>
          <div style={{ top: "25rem", left: "20rem" }}>
            <Backend
              emoji={Humble}
              heading={"Back-End"}
              details={"node, mongoDB, Express Js"}
              html={nodejs}
              css={mongodb}
              js={expressjs}
            />
          </div>
          <div className="blur s-blur2" style={{background:"var(--purple"}}></div>
        </div>
      </div>
    </div>
  );
}
