import "./Home.css";
import Github from "../../img/github.png";
import LinkedIn from "../../img/linkedin.png";
import Vector1 from "../../img/Vector1.png";
import Vector2 from "../../img/Vector2.png";
import sonia from "../../img/sonia.png";
import thumbup1 from "../../img/thumbup1.png";
import Crown from "../../img/crown.png";
import grassesimoji from "../../img/glassesimoji.png";
import FloatingDiv from "../FloatingDiv/FloatingDiv";

export default function Home() {
  return (
    <div id="home">
      <div className="intro">
        <div className="i-left">
          <div className="i-name">
            <span>Hey! I Am</span>
            <span>Sonia Diwedi</span>
            <span>
              I am a passionate self-taught full stack web developer from India.
              My passion for software lies with dreaming up ideas and making
              them come true with elegant interface.
            </span>
          </div>
          <button className="button i-button">Hire me</button>
          <div className="i-icons">
            <img src={Github} alt="" />
            <img src={LinkedIn} alt="" />
          </div>
        </div>
        <div className="i-right">
          <img src={Vector1} alt="" />
          <img src={Vector2} alt="" />
          <img style={{borderRadius:"50px",width:"30%",}} src={sonia} alt="" />
          {/* <img src={thumbup1} alt="" /> */}

          <div style={{ top: "-4%", left: "68%" }}>
            {/* <FloatingDiv
              image={Crown}
              text1="Full Stack"
              text2="Web Developer"
            /> */}
          </div>
          <div style={{ top: "15rem", left: "-4rem", width: "20%" }}>
            {/* <img src={thumbup1} alt="" /> */}
          </div>

          <div
            className="blur"
            style={{ background: "rgb (238 210 255)" }}
          ></div>
          <div
            className="blur"
            style={{ background: "#C1F5FF",top:"17rem",width:"21rem",height:"11rem",left:"-9rem" }}
          ></div>
        </div>
      </div>
    </div>
  );
}
